import React, { Component } from "react";
import Myjson from "./data.json";
import { Header } from './Header';
import { Body } from './Body';
import { Sidebar } from './Sidebar';
import { Footer } from './Footer';
import "./css/App.css";
import "./css/myquality.css";
import './css/oil.css';
import './css/btncool.css';
import './css/snikos3d.css';

export default class App extends Component {

    constructor(props) {
      super(props);
      this.state = {
        listFoo: ["CV Powered by: ", "Javascript", "React", "Components", "Css3 Animation", "Three.js", "TweenMax.js"],
        start: Myjson
      }
    }
    
    render() {
        return (
          <>
            <div className="row bg-light">
              <Header list={ this.state.start } />
            </div>
            <div className="row">
              <Body exp={ this.state.start } />
              <Sidebar exp={ this.state.start } />
            </div>
            <div className="row bg-light">
              <Footer lists={ this.state.listFoo } />
            </div>
          </>
        );
    }
}
