import React, { Component } from 'react';

export default class Logos extends Component {
	constructor(props){
		super(props);
		this.state = {
			toggleClass: "hover",
			timer: 0
		}
		this.playLogo(10000);
	}

	playLogo(per){
		this.timer = setTimeout( () => {
			this.handleClick();
			this.playLogo(per);
		}, per);
	}

	handleClick = () => {
		this.setState({
			toggleClass: (this.state.toggleClass==="" ? "hover" : "")
		});
	}

	render() {
		return (
			<>
			<div className={`oil ${this.state.toggleClass}`} onClick={ this.handleClick }>
			  <span className="b">
				<figure className="ox"></figure>
			  </span>
			  <span className="b">
				<figure className="ox"></figure>
			  </span>
			  <span className="b">
				<figure className="ox"></figure>
			  </span>
			  <span className="b">
				<figure className="ox"></figure>
			  </span>
			</div>
		  </>
		)
	}
}