import React from 'react';
import { SidePersInfo } from './sidebar/SidePersInfo';
import { SideSkills } from './sidebar/SideSkills';

export function Sidebar(props){
	return (
		<>
    <div className="col-12 col-sm-12 col-md-4 col-lg-3 bg-light">
      <SidePersInfo personal_info={ props.exp.personal_info } />
      <SideSkills other_my_skills={ props.exp.experiences.also_skills } />
    </div>
	  </>
	)
}