import React from 'react';

export function Experiences(props){
	return (
		<>
		  <h6 className="cv-h6 text-info">{ props.expers.title }</h6>
		  <ul className="row cv-ul">
      {
      	props.expers.list.map( (item, idx) => {
      		return <li key={item} className="col-6 col-sm-4 col-md-4 col-lg-3">
          <div className="btn-boom btn-boom-cv boom-c-1">
            <div className="boom-hover">
              <span className="out-text">{ item.split(":")[0] }</span>
            </div>
            <div className="boom-shadow"></div>
            <div className="boom-overlay"></div>
            <div className="boom-inner">
              <b className="cv-ul__b2">Level: { item.split(":")[1] }</b>
            </div>
          </div>
      	</li>;
      })}
      </ul>
		</>
	)
}