import React, { Component } from "react";

export default class Myquality extends Component {
	constructor(props){
		super(props);
		this.state = {
			toggleClass: ""
		}
		this.startGo();
	}

	startGo(){
		setTimeout( () => {
			this.setState({
				toggleClass: "go"
			});
		}, 1500);
	}

	handleClick = () => {
		this.setState({
			toggleClass: (this.state.toggleClass!=="go" ? "go" : "")
		});
	}

	render() {
		return (
		<>
			<div className={`my-quality ${ this.state.toggleClass }`} onClick={ this.handleClick }>
				<div className="my-quality_block my-quality_main">
				{ this.props.lists.map( (item, idx) => {
					if( idx === 0 ){
						return <span key={ item } className="my-quality_title">{ item }</span>
					} else {
						return <span key={ item } className={`my-quality_subblock my-quality_box${idx}`}>
							<span className="my-quality_subtitle">{ item }</span>
						</span>
					}
				}) }
				</div>
			</div>
	  </>
		)
	}
}