import React from 'react';
import Myquality from './components/Myquality';
import { Description } from './components/Description';
import { Languages } from './components/Languages';
import { Experiences } from './components/Experiences';
import { Snikos3d } from './components/Snikos3d';

export function Body(props){
	return (
		<>
    <div className="col-12 col-sm-12 col-md-8 col-lg-9">
      <Snikos3d />
      <Description desc={ props.exp.description } />
      <Myquality lists={ props.exp.qualities }/>
      <Experiences expers={ props.exp.experiences }/>
      <Experiences expers={ props.exp.experiencesAlso }/>
      <Languages langs={ props.exp.languages } />
    </div>
	  </>
	)
}